import { Modal } from "bootstrap";

export default {
  methods: {
    showModal() {
      // input type=file
      if (this.$refs.fileInput) {
        this.$refs.fileInput.value = "";
      }
      this.modal.show();
    },
    hideModal() {
      this.modal.hide();
    },
  },
  // modal
  mounted() {
    this.modal = new Modal(this.$refs.modal);
  },
};
